:root {
  --bg-color :rgb(226 236 236);
  --content-color: rgb(233, 242, 242);
}

.d-flex {
  display: flex;
}

.mx-1 {
  margin: 0px 20px;
}

.mx-05 {
  margin: 0px 15px;
}

.my-1 {
  margin-top: 30px;
}

.content-margin {
  margin: 10px 15px;
}

.focus:focus {
  border: 2px solid #007bff;
  outline: none;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.hover:hover {
  background-color: rgb(210, 231, 231);
  color: black;
  cursor: pointer;
}

.row {
  display: flex;
}
.flex-wrap {
  flex-wrap: wrap;
}
.justify-content-evenly {
  justify-content: space-evenly;
}