a {
    text-decoration: none;
}
.footer {
    justify-content: space-between;
    align-items: center;
    padding: 10px 10px;
    margin-top: 30px;
    margin-bottom: 15px;
    background-color: var(--content-color);
    box-shadow: 2px 2px 5px rgb(200 200 200);
    border-radius: 10px;
}
.pageNo {
    font-size: 14px;
}
.pagination {
    justify-content: space-evenly;
    width: 430px;
    align-items: center;
    font-size: 14px;
    font-weight: 700;
}
.pagination a {
    color: black;
    background-color: white;
    padding: 7px;
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 11px;
    box-shadow: 3px 3px 5px rgb(200 200 200);
}