@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
hr {
    opacity: 0.5;
}
.content {
    border-radius: 10px;
    box-shadow: 2px 2px 5px rgb(200 200 200);
    background-color: var(--content-color);
    width: 360px;
    height: 445px;
    font-family: 'Poppins', sans-serif;
    flex: 0 0 calc(30% - 0px);
}
.nameInfo {
    justify-content: space-between;
}
.carImg{
    /* background: url('Img/bmw3Series.webp'); */
    height: 254px;
    margin: 5px 5px;
    background-position: center;
    background-size: cover;
    border-radius: 14px;
    display: flex;
    justify-content: center;
}
.carImg img {
    width: -webkit-fill-available;
    border-radius: 7px;
}

.carName {
    font-size: 20px;
}
.content i {
    color: #0d94ff;
}
.info div {
    width: 50%;
}
.info div span {
    margin: 0px 7px;
}
.releaseDate {
    font-size: 14px;
    font-weight: 600;
    border: 2px dashed #0d94ff;
    padding: 4px 13px;
    border-radius: 16px;
}
.priceSec {
    align-items: center;
    justify-content: space-between;
}
.priceSec span p {
    font-size: 12px;
    font-weight: 700;
}
.price {
    font-size: 24px;
    font-weight: 100;
}
.rentSec {
    display: flex;
    align-items: center;
}
.rentSec i {
    margin: 0px 15px;
    background-color:var(--bg-color);
    padding: 8px;
    border-radius: 10px;
}
.rentButton {
    border: none;
    background-color: #0d94ff;
    color: white;
    padding: 11px 13px;
    border-radius: 13px;
    font-weight: 600;
    font-size: 13px;
}