.navbar {
    background-color: var(--content-color);
    border-radius: 18px;
    padding: 10px 28px;
    align-items: center;
    box-shadow: 2px 2px 5px rgb(200 200 200);
}
.search {
    align-items: center;
    width: 204px;
    justify-content: space-around;
    margin: 0px 20px;
}
#input {
    border: none;
    padding: 7px 15px;
    font-size: 16px;
    border-top-left-radius: 9px;
    border-bottom-left-radius: 9px;
    outline: none;
}
.icon {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    padding: 8.5px 8px;
    border-top-right-radius: 9px;
    border-bottom-right-radius: 9px;
    cursor: pointer;
}
.buttons {
    margin: 0 40px;
    width: 230px;
    justify-content: space-around;
    font-size: 16px;
}
.buttons a {
    color: black;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 95px;
    padding: 8px;
    border-radius: 7px;
}

.buttons i {
    font-size: 13px;
}